      font-size: 28px;
    }
  
    h5 {
      font-size: 20px;
    }
  
    h6 {
      font-size: 18px;
    }
  
    .section-padding {
      padding-top: 50px;
      padding-bottom: 50px;
    }
    

  
   
  
    .hero-section {
      padding-top: 150px;
    }
  
    .pricing-thumb {
      padding: 35px;
    }
  
    .schedule-table h3 {
      font-size: 22px;
    }
  
    .schedule-table th {
      padding: 20px;
    }
  
    .schedule-table tr, 
    .schedule-table td {
      padding: 25px;
    }
  
    .ticket-section {
