  @media screen and (max-width: 767px) {
    .custom-btn {
      font-size: 14px;
      padding: 10px 20px;
    }
  }
  
  @media screen and (max-width: 480px) {
    h1 {
      font-size: 52px;
    }
  
    h2 {
      font-size: 28px;
    }
  
    h3 {
      font-size: 26px;
    }
  
    h4 {
      font-size: 22px;
    }
  
    h5 {
      font-size: 20px;
    }
  }
  
  